<!-- 歌手页面的主题 -->
<template>
    <scroll class="listview" :data="data"
    ref="listview" 
    :listenScroll="listenScroll"
    :probeType="probeType"
    @scroll="scroll">
        <ul>
            <li v-for="group in data" class="list-group" ref="listGroup">
                <h2 class="list-group-title">{{group.title}}</h2>
                <ul>
                    <!--添加点击事件，点击跳转子页面  -->
                    <li @click="selectItem(item)" v-for="item in group.items" class="list-group-item">
                        <img class="avatar" v-lazy="item.avatar" />
                        <span class="name">{{item.name}}</span>
                    </li>
                </ul>
            </li>
        </ul>
        <div class="list-shortcut" @touchstart="onShortcutTouchStart"
        @touchmove.stop.prevent="onShortcutTouchMove">
            <ul>
                <li  v-for="(item,index) in shortcutList"
                 class="item" 
                 :data-index="index" 
                 :class="{'current': currentIndex === index}">
                    {{item}}
                </li>
            </ul>
        </div>
        <!--滚到对应位置自动吸附,在大于0时候不显示，不显示时候finxedTitle正好是一个空字符串  -->
        <div class="list-fixed" v-show="finxedTitle" ref="fixed">
            <h1 class="fixed-title">{{finxedTitle}}</h1>
        </div>
        <!-- data(props里面的data) -->
        <!--loading组件  -->
        <div class="loading-container" v-show="!data.length">
            <loading></loading>
        </div>
    </scroll>
</template>

<script type="text/ecmascript-6">
    import Scroll from 'base/scroll/scroll'
    import {getData} from 'common/js/dom'
    import Loading from 'base/loading/loading'
    //右侧快速查找字母的每个里的高度.格式固定的
    const ANCHOR_HEIGHT = 18
    // 固定标题的高度
    const TITLE_HEIGHT = 30

    export default {
        created() {
            this.touch = {}
            this.listenScroll = true
            this.listHeight = []
            this.probeType = 3
        },
        data() {
            return {
                scrollY: -1,
                // 当前应该显示哪个
                currentIndex: 0,
                // 两个区块交替时候。固定标题应该往上面去一点
                diff: -1
            }
            
        },
        props: {
            // 接受调用listview组件地方data传进来的值
            data: {
                type: Array,
                default: []
            }
        },
        computed: {
            // 右侧快速入口（ABC导航）
            shortcutList() {
                return this.data.map((group) => {
                    return group.title.substr(0, 1)
                })
            },
            finxedTitle() {
                // 滚动高度大于0的话，应该是空内容，且隐藏
                if(this.scrollY > 0) {
                    return ''
                }
                // this.data一开始是没有的，空数组
                return this.data[this.currentIndex] ? this.data[this.currentIndex].title : ''
            }
        },
        methods: {
            // 派发点击的元素(数据)，让外部知道
            selectItem(item) {
                this.$emit('select', item)
            },
            onShortcutTouchStart(e) {
                let anchorIndex = getData(e.target, 'index')
                let firstTouch = e.touches[0]
                this.touch.yStart = firstTouch.pageY
                // 记录点之前的描点位置
                this.touch.anchorIndex = anchorIndex
                // console.log(this.$refs.listview)
                this._scrollTo(anchorIndex)
            },
            onShortcutTouchMove(e) {
                let firstTouch = e.touches[0]
                this.touch.yEnd = firstTouch.pageY
                // 计算偏移了几个锚点
                let delta = Math.floor((this.touch.yEnd - this.touch.yStart)/ANCHOR_HEIGHT)
                // 计算滚动时候是第几个锚点
                let anchorIndex = parseInt(this.touch.anchorIndex) + delta
                this._scrollTo(anchorIndex)
            },
            // 暴露借口给singer列表出迷你播放器时候刷新用
            refresh() {
                this.$refs.listview.refresh()
            },
            // beteer-scorll派发的事件。右侧快速入口的点击和变色无关，只是去监听的。所以在点击滚动定位时候要关联一下
            scroll(pos) {
                this.scrollY = pos.y
            },
            _scrollTo(index) {
                // 左右关联起来。利用scroll监听滚到哪里了
                // 上下2个小块的index是null
                if(!index && index !== 0) {
                    return
                }
                // 过界处理
                if(index<0) {
                    index = 0
                }
                // this.listHeight比dom多2个
                if(index>this.listHeight.length - 2) {
                    index = this.listHeight.length - 2
                }
                this.scrollY = -this.listHeight[index]
                // console.log(index,-this.listHeight[index])
                this.$refs.listview.scrollToElement(this.$refs.listGroup[index], 0 )
            },
            // 计算大的滚动列表的高度
            _calculateHeiget() {
                this.listHeight = []
                const list = this.$refs.listGroup
                let height= 0
                this.listHeight.push(height)
                // listHeight的长度比实际dom多一个，因为listheight第一个对应dom第一个上线，而第二个对应dom第二个下限。故多一个
                for(let i=0; i<list.length; i++) {
                    let item = list[i]
                    // 每一次都加一点
                    height += item.clientHeight
                    this.listHeight.push(height)
                }
            }
            
        },
        watch: {
            // 监视data里面的变化，即scrollY和currentIndex.获取歌手数据
            data() {
                setTimeout(() => {
                    this._calculateHeiget()
                },20)
            },
            // 计算落在哪个区间
            scrollY(newY) {
                const listHeight = this.listHeight
                // 当滚动到顶部时候，即newY>0
                if(newY>0) {
                    this.currentIndex = 0
                    return
                }
                // 中间部分滚动
                for(let i=0; i<listHeight.length; i++) {
                    let height1 = listHeight[i]
                    let height2 = listHeight[i+1]
                    // 如果是最后一个的时候，listHeight[i+1]不存在的.或者向上滚动的时候
                    if(!height2 || (-newY>=height1 && -newY<height2)) {
                        this.currentIndex = i
                        // console.log(this.currentIndex)
                        // 滚动时候固定标题和下一个区块的是否重叠，重叠的话固定标题往上顶一点.上限减滚动的距离
                        this.diff = height2 + newY
                        return 
                    }
                }
                // 地步滚动，且-newY大于最后一个元素的上限
                // 为什么大于-2？因为listHeight的长度比实际dom多一个，因为listheight第一个对应dom第一个上线，而第二个对应dom第二个下限。故多一个
                this.currentIndex = listHeight.length - 2
                
            },
           // 监视滚动时候固定标题和下一个区块的是否重叠，重叠的话固定标题往上顶一点.上限减滚动的距离。上面计算过了
            diff(newVal) {
                let fixedTop = (newVal>0 && newVal<TITLE_HEIGHT) ? newVal - TITLE_HEIGHT : 0
                if(this.fixedTop === fixedTop) {
                    return
                }
                this.fixedTop = fixedTop
                this.$refs.fixed.style['transform'] = `translate3d(0,${fixedTop}px,0)`
                this.$refs.fixed.style['webkitTransform'] = `translate3d(0,${fixedTop}px,0)`
            }
        },
        components: {
            Scroll,
            Loading
        }
    }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

  .listview
    position: relative
    width: 100%
    height: 100%
    overflow: hidden
    background: $color-background
    .list-group
      padding-bottom: 30px
      .list-group-title
        height: 30px
        line-height: 30px
        padding-left: 20px
        font-size: $font-size-small
        color: $color-text-l
        background: $color-highlight-background
      .list-group-item
        display: flex
        align-items: center
        padding: 20px 0 0 30px
        .avatar
          width: 50px
          height: 50px
          border-radius: 50%
        .name
          margin-left: 20px
          color: $color-text-l
          font-size: $font-size-medium
    .list-shortcut
      position: absolute
      z-index: 30
      right: 0
      top: 50%
      transform: translateY(-50%)
      width: 20px
      padding: 20px 0
      border-radius: 10px
      text-align: center
      background: $color-background-d
      font-family: Helvetica
      .item
        padding: 3px
        line-height: 1
        color: $color-text-l
        font-size: $font-size-small
        &.current
          color: $color-theme
    .list-fixed
      position: absolute
      top: 0
      left: 0
      width: 100%
      .fixed-title
        height: 30px
        line-height: 30px
        padding-left: 20px
        font-size: $font-size-small
        color: $color-text-l
        background: $color-highlight-background
    .loading-container
      position: absolute
      width: 100%
      top: 50%
      transform: translateY(-50%)
</style>



